    width: 25px;
    min-width: 25px;
    height: 10px;
    position: relative;
    margin: 0 0 6px 6px;
    transform: scale(0.8);
    transition: 0.3s ease-in-out;
    transition-delay: 0s; }
    .link > i:before, .link > i:after {
      content: '';
      position: absolute;
      top: 0; }
    .link > i:before {
      width: 20px;
      height: 2px;
      bottom: 0;
      left: 0;
      margin: auto;
      background: #55C4DA;
      transform-origin: 100% 0;
      transition: 0.3s ease-in-out;
      transition-delay: 0s; }
    .link > i:after {
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 0 5px 7px;
      border-color: transparent transparent transparent #55C4DA;
      right: 0; }
  .link > span {
    position: relative; }
    .link > span:after {
      content: '';
      height: 1px;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      background: currentColor;
      transform-origin: 50% 50%;
      transition: 0.3s ease-in-out; }
  .link:hover > i, .link:focus > i {
    transition-delay: 0.1s;
    transform: scale(0.8) translate3d(-10px, 0, 0); }
    .link:hover > i:before, .link:focus > i:before {
      transition-delay: 0.15s;
      transform: scaleX(0.7); }
  .link:hover > span:after, .link:focus > span:after {
    transform: scaleX(0.9); }
  .link.small > i {
    margin-bottom: 4px;
    transform: scale(0.6); }
  .link.small:hover > i, .link.small:focus > i {
    transform: scale(0.6) translate3d(-10px, 0, 0); }

